import React, { useEffect } from "react";
import { useTypedSelector } from "../hooks/useTypedSelector";
import { useActions } from "../hooks/useActions";

export const TodoList = (props: any) => {
	const { todos, isLoading, error, page, limit  } = useTypedSelector(state => state.todo);
	const pages: number[] = [1,2,3,4,5];
	const { fetchTodos, setTodoPage } = useActions();
	useEffect(() => {
		fetchTodos(page, limit, 'data');
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [page]);

	if (isLoading) {
		return (<p>Идет загрузка</p>)
	}

	if(error) {
		return (<p>Произошла ошибка</p>)
	}

	return ( 
		<div>
			{todos.map( (todo: any, i: number) => (
				<div key={`${i}`}>{todo.id}</div>
			))}
			<ul className="dpl-flex">
				{pages.map( (p: any) => (
					<li
					  onClick={ () => setTodoPage(p) }
					  className={`p-10 ${p===page ? 'App-pagination__active' : 'App-pagination__page'}`}
					  key={`${p}`}>
					    <span>{p}</span>
					</li>
				))}
			</ul>
		</div>
	)
}