import React from "react";
import { useActions } from "../hooks/useActions";
import { useTypedSelector } from "../hooks/useTypedSelector";

export const SelectFile = () => {
	const { isLoading, error, page, limit  } = useTypedSelector(state => state.todo);
	const [file, setFile] = React.useState('data');
	const [selectValue, setSelectValue] = React.useState('data');
	const { fetchTodos, setTodoFile } = useActions();

	React.useEffect(() => {
		fetchTodos(page, limit, file);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [file]);

	const OnSelectFile = (event: any) => {
		let selFile = event.target.value;
		if( selFile !== file ) setFile(selFile);
		
		setTodoFile(selFile);
		setSelectValue(selFile);
	}

	if (isLoading) {
		return (<p>Идет загрузка</p>)
	}

	if(error) {
		return (<p>Произошла ошибка</p>)
	}

	return (
		<select value={selectValue} onChange={(e) => OnSelectFile(e)}>
			<option value="data">data</option>
			<option value="ass">ass</option>
		</select>
	);
}